@import url('https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;0,1000;1,200&display=swap');
@import url('./colors.css');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Nunito', arial;
}

body{
    display: flex;
    justify-content: center;
    height: 100vh;
    background-color: tomato;
}

::-webkit-scrollbar {
    display: none;
}


.view-point{
    background: var(--main-green);
    background: linear-gradient(144deg, var(--main-green) 0%, rgb(8, 8, 8) 27%, rgb(8, 8, 8) 100%);
    height: 100%;
    width: 360px;
    border-radius: 25px;
    display: grid;
    grid-template-rows: 1fr auto;
}
.menu{
    display: flex;
    justify-content: space-evenly;
    bottom: 0;
    gap: 35px;
    height: 60px;
    background-color: var(--main-gray);
    align-items: center;
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
}
.menu-item{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4px;
    cursor: pointer;
    flex: 1 1 0;
}

.menu-item__icon{
    color: gray;
    transform: scale(1.05);
    transition: all 0.2s ease-in-out;
}

.menu-item:hover .menu-item__icon{
    color: white;
}

.menu-item__label{
    color: white;
    font-weight: lighter;
    font-size: 9px;
}