@import "https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;0,1000;1,200&display=swap";
:root {
  --main-gray: #343434;
  --dark-gray: #080808;
  --main-green: #006b12;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Nunito, arial;
}

body {
  height: 100vh;
  background-color: tomato;
  justify-content: center;
  display: flex;
}

::-webkit-scrollbar {
  display: none;
}

.view-point {
  background: var(--main-green);
  background: linear-gradient(144deg, var(--main-green) 0%, #080808 27%, #080808 100%);
  height: 100%;
  width: 360px;
  border-radius: 25px;
  grid-template-rows: 1fr auto;
  display: grid;
}

.menu {
  height: 60px;
  background-color: var(--main-gray);
  border-bottom-right-radius: 25px;
  border-bottom-left-radius: 25px;
  justify-content: space-evenly;
  align-items: center;
  gap: 35px;
  display: flex;
  bottom: 0;
}

.menu-item {
  cursor: pointer;
  flex-direction: column;
  flex: 1 1 0;
  align-items: center;
  gap: 4px;
  display: flex;
}

.menu-item__icon {
  color: gray;
  transition: all .2s ease-in-out;
  transform: scale(1.05);
}

.menu-item:hover .menu-item__icon {
  color: #fff;
}

.menu-item__label {
  color: #fff;
  font-size: 9px;
  font-weight: lighter;
}

